import React, { useState, useEffect, useContext } from "react";
import "./New.css";
import { db, colRef } from "../firebase/firebase";
import {
  collection,
  getDocs,
  addDoc,
  deleteDoc,
  doc,
  onSnapshot,
  updateDoc,
} from "firebase/firestore";
import { UserContext } from "../context/context";
import ModalComponent from "./Modal";

function New() {
  const { number, setNumber } = useContext(UserContext);
  const [modalIsOpen, setIsOpen] = React.useState(false);

  const colRefNum = collection(db, "number");

  useEffect(() => {
    onSnapshot(colRefNum, (snapshot) => {
      snapshot.docs.forEach((doc) => {
        console.log(doc.data());
        console.log(doc.data().number);

        setNumber(doc.data().number);
      });
    });
  }, []);

  // getDocs(colRef)
  // .then((snapshot) => {

  // })
  // .catch((err) => {
  //   console.log(err.message);
  // });

  const [client, setClient] = useState("");
  const [description, setDescription] = useState("");
  let date = new Date();

  let day = date.getDate();
  const months = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];
  let month = months[date.getMonth()];

  let year = date.getFullYear();

  let convertedDate = `${day} ${month} ${year}`;
  console.log(convertedDate);
  const submitHandler = (e) => {
    console.log("test");
    e.preventDefault();
    const cotations = {
      client: client,
      description: description,
      number: number,
      date: convertedDate,
    };
    addDoc(colRef, cotations).then(() => {
      setClient("");
      setDescription("");
    });
    // setNumber((prevNumber) => {
    //   return prevNumber + 1;
    // });
    setIsOpen(true);

    const docRef = doc(db, "number", "thenumber");
    updateDoc(docRef, {
      number: number + 1,
    });
  };
  // const deleteHandler = (e)=>{
  //   e.preventDefault();
  //   const docRef = doc(db, 'cotations', deleteIDHERE )
  // deleteDoc(docRef).then((=>{
  // nothing
  // }))
  // }
  return (
    <div className="new">
      <ModalComponent modalIsOpen={modalIsOpen} setIsOpen={setIsOpen} title='Well done!' content={`Votre cotation a été enregistré sous le numéro  ${number -1 }`}/>
      <div className="CotationForm">
        <form onSubmit={submitHandler}>
          <h3 style={{ textAlign: "center" }}> Nouvelle Cotation </h3>
          
          <input
            required
            placeholder="Nom du Client"
            value={client}
            onChange={(e) => setClient(e.target.value)}
          />{" "}
          <br />
          <textarea
            required
            value={description}
            placeholder="Description"
            rows="12"
            cols="30"
            onChange={(e) => setDescription(e.target.value)}
          />{" "}
          <br />
          <button className="btn" type="submit">
            Enregistrer
          </button>
          {/* Delete button  */}
          {/* <button className="btn" onClick={deleteHandler}>
            Enregistrer
          </button> */}
        </form>
      </div>
    </div>
  );
}

export default New;
