import React, { useState, useEffect } from "react";
import "./Cotations.css";
import {
  getFirestore,
  collection,
  getDocs,
  onSnapshot,
  query,
  orderBy,
  where,
  limit,
} from "firebase/firestore";
import { colRef } from "../firebase/firebase";
import ModalComponent from "./Modal";
function Cotations() {
  const [search, setSearch] = useState("");
  const [data, setData] = useState();
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [cotModal, setCotModal] = useState(true);
  const [id, setId] = useState("");

  // const deleteHandler = (e)=>{
  //   e.preventDefault();
  //   const docRef = doc(db, 'cotations', deleteIDHERE )
  // deleteDoc(docRef).then((=>{
  // nothing
  // }))
  // }
  const q = query(colRef, orderBy("number", "desc"));

  useEffect(() => {
    onSnapshot(q, (snapshot) => {
      let cotations = [];
      snapshot.docs.forEach((doc) => {
        cotations.push({ ...doc.data(), id: doc.id });
      });
      console.log(cotations);
      console.log(q);
      setData(cotations);
    });
  }, []);
  console.log(data);
  const typingHandler = (e) => {
    setSearch(e.target.value);
  };
  let filtered = null;
  if (data) {
    filtered = data.filter((item) =>
      item.client.toLowerCase().includes(search.toLowerCase())
    );
    console.log("infinite loop");
  }
  const ModalOpen = (id) => {
    setIsOpen(true);
    setId(id);
  };

  return (
    <div className="cotations">
      <ModalComponent
        modalIsOpen={modalIsOpen}
        setIsOpen={setIsOpen}
        title="Are you sure?"
        cotModal={cotModal}
        id={id}
      />

      <input type="text" placeholder="Nom du client" onChange={typingHandler} />
      <div className="container">
        {search &&
          filtered &&
          filtered.length > 0 &&
          filtered.map((item, index) => (
            <div key={index} className="cotation">
              <p id="num">
                Numéro <span style={{ fontWeight: "bold" }}>{item.number}</span>
              </p>
              <p id="date">
                {" "}
                Créée le{" "}
                <span style={{ fontWeight: "bold" }}> {item.date}</span>
              </p>
              <div className="flex">
                <p>Pour : {item.client} </p>
                <p>Description : {item.description}</p>
              </div>
            </div>
          ))}
        {!search &&
          data &&
          data.length > 0 &&
          data.map((item, index) => (
            <div key={index} className="cotation">
              <p id="num">
                Numéro <span style={{ fontWeight: "bold" }}>{item.number}</span>
              </p>
              <p id="date">
                {" "}
                Créée le{" "}
                <span style={{ fontWeight: "bold" }}> {item.date}</span>
              </p>
              <div className="flex">
                <p>Pour : {item.client} </p>
                <p>Description : {item.description}</p>
              </div>
              <div style={{ textAlign: "right" }}>
                <button className="cot" onClick={() => ModalOpen(item.id)}>
                  Supprimer
                </button>
              </div>
            </div>
          ))}
        {data && data.length === 0 && (
          <h1
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            Aucune Cotations.
          </h1>
        )}
        {data && data.length > 0 && filtered && filtered.length === 0 && (
          <h1
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            Aucun Client portant ce nom.
          </h1>
        )}
      </div>
    </div>
  );
}

export default Cotations;
