import React, { useContext } from "react";
import "./App.css";
import Header from "./components/Header";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import New from "./components/New";
import Cotations from "./components/Cotations";
import LogIn from "./components/LogIn";
import NewAccount from "./components/NewAccount";
import { UserContext } from "./context/context";

function App() {
  const { isLoggedIn, setIsLoggedIn } = useContext(UserContext);

  return (
    <BrowserRouter>
      <Header />
      <Routes>
        <Route exact path="/" element={!isLoggedIn ? <LogIn /> : <New/>} />
        <Route exact path="/new" element={isLoggedIn ? <New /> : <LogIn />} />
        <Route
          exact
          path="/cotations"
          element={isLoggedIn ? <Cotations /> : <LogIn />}
        />
        <Route exact path="NewAccount" element={!isLoggedIn ? <NewAccount />: <NewAccount/>} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
