import React,{useContext} from "react";
import "./Header.css";
import { BrowserRouter as Router, Route, Link, NavLink } from "react-router-dom";
import logo from "../assets/polytra.png";
import {auth} from '../firebase/firebase'
import { UserContext } from "../context/context";

function Header() {
  const { isLoggedIn, setIsLoggedIn } = useContext(UserContext);

  const logOutHandler = (e)=>{
    e.preventDefault();
    auth.signOut().then(()=>{
      console.log('signed out')
    })

  }
  return (
    <div className="header">
      <div className="logo">
        <img src={logo} />

        <h1 style={{color : 'white'}} >Système de Cotations</h1>
      </div>
      {isLoggedIn && <div className="liens">
        <span>
          <NavLink to="/new" activeClassName='active'>Nouvelle Cotation</NavLink>
        </span>
        <span>
          <NavLink to="/cotations" activeClassName='active'>Cotations</NavLink>
        </span>
        <span>
          <button className="bttn" onClick={logOutHandler} >Déconnexion</button>
        </span>
      </div>}
    </div>
  );
}

export default Header;
